<template>
    <div>
        <section class=" modals">
            <div class="container products">
                <div class="modal fade" id="searchModal" role="dialog" tabindex="-1" aria-hidden="true">
                    <div class="modal-dialog modal-lg modal-dialog-centered">
                        <div class="modal-content unread-messages" ref="load_licence">
                            <button type="button" style="display: none" class="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                            <div class="modal-body">
                                <div class="chatting-page" style="min-height: 400px;">
                                    <div class="container">
                                        <div class="row" ref="searchResult">
                                            <spinner :data="spinnerData"></spinner>
                                            <div class="space"></div>
                                            <div class="col-lg-12">
                                                <h4> Search Result for <small><code>{{ searchText }}</code></small></h4>
                                                <div class="space">

                                                </div>
                                            </div>

                                            <p v-if="searchResultFail === true"> {{ searchResultMessage }} </p>

                                            <span v-else style="width:100%">
                                                <div class="row" v-if="searchResult.length > 0">
                                                    <div v-for="(product, index) in searchResult" :key="index" class="col-lg-12 col-md-12 col-sm-12">
                                                        <article class="feature-full__job-posts">
                                                            <div class="row">
                                                                <div class="company-logo">
                                                                    <a href="#"><img style="width: 80px;height: 80px" :src="product.product_image" alt=""></a>
                                                                </div>
                                                                <div class="job-description">
                                                                    <h5 class="job-title"><a href="#">{{ product.product_description }} </a></h5>
                                                                    <span class="location"><a href="#"><i class="fas fa-map-marker-alt"></i> {{ product.product_name }}</a></span>
                                                                    <!--                                                                <span class="job-category cat-color-one"><a href="#">Web Design</a></span>-->
                                                                </div>
                                                                <div class="budget">
                                                                    <ul>
                                                                        <li class="amount"><p>₦{{ product.product_value }}</p></li>
                                                                    </ul>
                                                                </div>
                                                                <div class="apply">
                                                                    <button @click="selectProduct(product.product_id)" class="apply-btn">Choose</button>
                                                                </div>
                                                            </div>
                                                        </article>
                                                    </div>
                                                </div>
                                            </span>

                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
    export default {
        name: "searchModal",
        data() {
            return {
                spinnerData: {},
            }
        },
        props: {
            searchText: String,
            searchResult: {},
            searchResultMessage: String,
            searchResultFail: Boolean,
        },
        methods: {
            showLoading(options = {}) {
                let loader = this.spinnerData = {
                    spinner: 'Google',
                    isLoading: options.show !== undefined ? options.show : true ,
                    color: 'rgba(0,191,255,0.73)',
                    size: '60px',
                    options: 'center absolute',
                };
                return loader;
            },
            selectProduct(id) {
                $(".close").click();
                this.$router.push('licence/' + id );
            },
        },
        mounted() {
            this.showLoading();
            window.eventBus.$on("stopLoader", val => {
                if (val === true) {
                    this.showLoading({show:false});
                }else {
                    this.showLoading();
                }
            })
        },
    }
</script>

<style scoped>

</style>
