<template>
    <div>
        <section id="hero" style="background: url(/img/hero-bg.jpg);">
            <div class="special-phone-effect">
                <img src="/img/P6YUM71-2.png">
                <div class="box ">
                    <a href="http://www.studylab360.com/#/" target="_blank">
                        <div class="blit">
                            <img src="/img/companies/studylab_logo.33b7dd3d.png">
                        </div>
                    </a>
                    <a href="https://www.veritexapp.com/" target="_blank">
                        <div class="blit" style="background-color: rgb(53, 70, 86)">
                            <img src="/img/companies/veritex_business.png">
                        </div>
                    </a>
                    <a href="https://qampusplus.com/" target="_blank">
                        <div class="blit" style="background-color: rgb(53, 70, 86)">
                            <img src="/img/companies/qampus_logo.png">
                        </div>
                    </a>
                    <a href="https://www.revcollect.com/" target="_blank">
                        <div class="blit">
                            <img src="/img/companies/logo.png">
                        </div>
                    </a>
                </div>
            </div>
            <div class="container full-height-1">
                <div class="row full-height-1">
                    <div class="col-lg-10 col-md-12 col-sm-12" style="display: flex;align-items: center">
                        <div class="land-details">
                            <div class="hero-wrapper ">
                                <div class="hero-details ">
                                    <div class="hero-title">
                                        <h1>Introducing {{ $store.state.appName }}<br> Market Place for Services</h1>
                                        <div class="hero-text">
                                            <!--                                        <p>All about algebra</p>-->
                                        </div>
                                    </div>

                                    <form @submit.prevent="searchProducts" class="hero-form">
                                        <div class="job-category">
                                            <select id="select-job" v-model="formModelIndustry.industry_id">
                                                <option v-for="(industries, index) in industriesArr" :key="index" :value="industries.industry_id">{{ industries.industry_name }}</option>
                                            </select>
                                            <div class="icon-one">
                                                <span><i class="fas fa-map-marker-alt"></i></span>
                                            </div>
                                        </div>
                                        <div class="job-keyword">
                                            <input @keyup="storeSearchText" type="text" v-model="formModelIndustry.search" placeholder="Search Products" class="keyword">
                                            <div class="icon-two">
                                                <span><i class="fas fa-briefcase"></i></span>
                                            </div>
                                        </div>
                                        <button v-if="searchText" data-toggle="modal" data-target="#searchModal" class="hero-btn"><i class="fas fa-search"></i> Search</button>
                                        <button v-else class="hero-btn"><i class="fas fa-search"></i> Search</button>
                                    </form>

                                </div>

                                <div class="row industry" style="z-index: 2;margin-top: 10px" ref="load_industry">
                                    <div data-toggle="modal" data-target="#unread-messages" @click="getProducts(industry.industry_id)" class="col-lg-3 col-md-6 col-sm-12" v-for="(industry, index) in industriesArr " :key="index">
                                        <div class="category bg-white" style="min-height: 0">
                                            <div class="category__image">
                                                <img src="img/category/cat1.png" alt="">
                                                <span class="category__icon" :style=" 'font-size: 30px;color:' + industry.icon_color " ><i class="fas fa-atom"></i></span>
                                            </div>
                                            <div class="category__info">
                                                <h6><a data-toggle="modal" data-target="#unread-messages" href="javascript:;">{{ industry.industry_name }}</a></h6>
                                            </div>
                                        </div>
                                    </div>
                                    <div v-if="industriesArr_count > 4" class="getMore get-button bidding">
                                        <router-link to="/services"><button class="get-btn">See More</button></router-link>
                                    </div>
                                </div>

                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </section>
        <ProductsModal/>
        <search-modal :searchText="searchText" :searchResult="searchResult" :searchResultMessage="searchResultMessage" :searchResultFail="searchResultFail"  ></search-modal>
    </div>
</template>

<script>
    import ProductsModal from "../../components/Modals/ProductsModal";
    import searchModal from "../../components/Modals/searchModal";
    export default {
        title: 'Welcome',
        name: "index",
        data() {
            return {
                industriesArr: {},
                industriesArr_count: '',
                formModelIndustry: {
                    industry_id: '',
                    search: '',
                },
                searchText: '',
                searchResult: {},
                searchResultMessage: '',
                searchResultFail: false,
            }
        },
        components: {
            searchModal,
            ProductsModal,
        },
        mounted() {
          this.getIndustries();
            // console.log(this.getRandomColor())
        },
        methods: {
            scrollHandle(evt) {
                console.log(evt)
            },
            storeSearchText() {
                this.searchText = this.formModelIndustry.search;
            },
            searchProducts() {
                let id = this.formModelIndustry.industry_id;
                let str = this.formModelIndustry.search;
                if (this.formModelIndustry.search !== "") {
                    this.axios.get(`/industries/search/products/${id}/${str}`)
                        .then(response => {
                            window.eventBus.$emit("stopLoader",true);
                            if (response.data.data.status === false) {
                                this.searchResultFail = true;
                                this.searchResultMessage = response.data.message;
                            }else {
                                this.searchResultMessage = '';
                                this.searchResultFail = false;
                                console.log(response.data.data.products);
                                this.searchResult = response.data.data.products;
                            }
                        })
                        .catch(error => {
                            console.log(error)
                        })
                }
            },
            // generate random colors for industry icons
            getRandomColor() {
                var letters = '0123456789ABCDEF';
                var color = '#';
                for (var i = 0; i < 6; i++) {
                    color += letters[Math.floor(Math.random() * 16)];
                }
                return color;
            },
            showLoading() {
                let loader = this.$loader(this, this.$refs.load_industry, {
                    loaderType: "spinner",
                });
                return loader;
            },
            getProducts(id) {
                window.eventBus.$emit("industryId", id);
            },
            getIndustries() {
                this.axios
                    .get("industries/all")
                    .then(response => {
                        // console.log(response.data.data.industries);
                        let industries = response.data.data.industries;
                        let industryData = [];
                        for (var key in industries) {
                            var value = industries[key];
                            var formFields = {
                                industry_description: value.industry_description,
                                industry_id: value.industry_id,
                                industry_name: value.industry_name,
                                icon_color: this.getRandomColor(),
                            };
                            industryData.push(formFields);
                        };
                        this.industriesArr_count = response.data.data.industries.length;
                        this.industriesArr = industryData.slice(0, 4)
                    })
                    .catch(error => {
                        console.log(error.response);
                    });
            },
        },
    }
</script>

<style scoped>
    .hero-wrapper .hero-details .hero-title h1 {
        font-size: 35px;
    }
    .special-phone-effect {
        position: fixed;
        top: 35%;
        right: 20%;
        width: 180px;
        z-index: 1;
        transition: all 0.6s ease-in;
    }
    .special-phone-effect img {
        width: 100%;
        /*padding: 60px 0;*/
    }
    .special-phone-effect .box {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        z-index: 2;
        padding: 40px 10px;
    }
    .special-phone-effect .box a {
        float: left;
        width: 45%;
        margin: 8px 3px;
        height: 80px;
        position: relative;
    }
    .special-phone-effect .blit {
        background-color: #fff;
        border-radius: 4px;
        padding: 30px 5px;
        height: 100%;
        width: 100%;
    }
    .special-phone-effect .blit:hover {
        box-shadow: 0 8px 10px rgba(0,0,0,.1);
    }
    @media (max-width: 991px) {
        .special-phone-effect {
            display: none;
        }
    }
    @media (min-width: 991px) and (max-width: 1326px) {
        .special-phone-effect {
            -webkit-transform: rotate(90deg);
            -ms-transform: rotate(90deg);
            transform: rotate(90deg);
            top: 40%;
            right: 14%;
            transition: all 0.6s ease-in;
        }
    }
    .category {
        text-align: center;
        padding-top: 16px;
        padding-bottom: 35px;
        border: 1px solid var(--border-one);
        border-radius: 10px;
        transition: all 0.5s;
    }
    .getMore {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        padding: 10px;
    }
</style>
